@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@400;700&display=swap);
*{box-sizing:border-box;padding:0;margin:0}a{text-decoration:none}body{font-family:'Raleway', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-color:#292828;color:white;max-width:1000px;margin:0px auto}a{color:lightsalmon}

.App{text-align:left;padding:20px}.match-won-card{background-color:#4da375}.match-lost-card{background-color:#a34d5d}

.TeamPage{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-template-rows:1fr 1fr 1fr;grid-column-gap:10px;grid-row-gap:10px}.TeamPage .team-name-section{grid-column:span 3;justify-self:start;align-self:end}.TeamPage .team-name{font-size:4rem;margin-bottom:20px}.TeamPage .win-loss-section{justify-self:center;padding:30px;text-align:center}.TeamPage .match-detail-section{grid-column:span 4}.TeamPage .more-link{font-size:1.5rem;justify-self:center;-webkit-align-self:center;align-self:center}.TeamPage .latest-matches{margin-bottom:5px}

.MatchDetailsCard{display:grid;grid-template-columns:1fr 1fr;margin-bottom:20px;padding:10px}.MatchDetailsCard .match-date,.MatchDetailsCard .match-venue,.MatchDetailsCard .match-result{margin-top:10px}.MatchDetailsCard .additional-details{text-align:right}.MatchDetailsCard .additional-details>p{margin-bottom:20px}

.MatchSmallCard{margin-bottom:20px;padding:10px}.MatchSmallCard .match-result{margin-top:10px}

.YearSelector{text-align:right}.YearSelector li{list-style:none;font-size:1.2rem;margin:5px auto}

.MatchPage{display:grid;grid-template-columns:1fr 5fr;grid-column-gap:20px}.MatchPage .year-selector{justify-self:end;margin:80px auto}.MatchPage .match-page-heading{margin:10px auto}


.HomePage .teams-grid{display:grid;grid-template-columns:1fr 1fr;grid-column-gap:25px;grid-row-gap:25px}.HomePage .teams-grid div{height:150px;text-align:center;padding-top:50px;border:1px solid white}.HomePage .home-page-header{font-size:4rem;margin-bottom:10px}.HomePage .home-page-subheader{font-size:1.5rem;margin-bottom:20px}

